import * as React from 'react';
import { useState } from 'react';
import { ISidebar } from '../../models/sidebar-model';
import { useNavigate } from 'react-router-dom';

export default function Sidebar() {

  const navigate = useNavigate();

  const [list, setList] = useState<ISidebar[]>([
    { name: 'Contacts', isActive: false, path: '/' },
    { name: 'Map And Charts', isActive: false, path: '/map' },
  ]);

  const handleItemClick = (index: number) => {
    const updatedList = list.map((item, i) => {
      if (i === index) {
        return { ...item, isActive: true };
      } else {
        return { ...item, isActive: false };
      }
    });
    setList(updatedList);
  };

  const handleClick = (path: string) => {
    navigate(path);
  };


  React.useEffect(() => {
    handleItemClick(0);
    handleClick("/")
  }, [])


  return (
    <>
      <button data-drawer-target="default-sidebar" data-drawer-toggle="default-sidebar" aria-controls="default-sidebar" type="button" className="inline-flex items-center p-2 mt-2 ml-3 text-sm text-gray-500 rounded-lg sm:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600">
        <span className="sr-only">Open sidebar</span>
        <svg className="w-6 h-6" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
          <path clip-rule="evenodd" fill-rule="evenodd" d="M2 4.75A.75.75 0 012.75 4h14.5a.75.75 0 010 1.5H2.75A.75.75 0 012 4.75zm0 10.5a.75.75 0 01.75-.75h7.5a.75.75 0 010 1.5h-7.5a.75.75 0 01-.75-.75zM2 10a.75.75 0 01.75-.75h14.5a.75.75 0 010 1.5H2.75A.75.75 0 012 10z"></path>
        </svg>
      </button>

      <aside id="default-sidebar" className="fixed left-0 z-40 w-64 h-screen transition-transform -translate-x-full sm:translate-x-0" aria-label="Sidebar">
        <div className="h-full pt-2 px-3 py-4 overflow-y-auto bg-black-50 dark:bg-black-800">
          <ul className="space-y-2 font-medium">
            {list.map((item, index) => (
              <li
                key={index}
                className={`${item.isActive ? 'bg-gray-500 text-white' : 'bg-transparent text-white'
                  } border-b p-4 cursor-pointer`}
                onClick={() => {
                  handleItemClick(index);
                  handleClick(item.path);
                }}
              >
                <span className="ml-3"> {item.name}</span>
              </li>
            ))}
          </ul>
        </div>
      </aside>
    </>
  );
}
